import React, {Fragment, useContext, useEffect} from 'react';
import MuiFormControl from './mui-form-control';
import BSFormControl from './bs-form-control';
import { IRenderField } from '../../constants/common-interface';
import FormContext from '../form-context';

function FormControl({theme, props, cField, sync}:{theme: string, props: IRenderField, cField: any, sync: Function}) {
   
    switch (theme) {
        case 'mui':
            return (
                <MuiFormControl {...props} sync={sync}/>
            )
        case 'tamagui':
            return (
                // <TamaGuiFormControl {...props} />
                <Fragment></Fragment>
            )
        case 'bootstrap':
        default:
            return (<BSFormControl {...props} sync={sync}/>)
    }
}

export default FormControl;