import React, { Fragment, useEffect } from 'react';
import FormUtils from '../../utils/FormUtil';
import { IField, IFieldChange } from '../../constants/model-interfaces';
import FormContext from '../form-context';
import { IRenderField } from '../../constants/common-interface';
export default abstract class BasicFormControl extends React.Component {
    static contextType = FormContext;
    displayType: string | undefined;
    field: IField;
    isFormControl: boolean;
    uuid: string;
    section: string;
    validation: {required: boolean|undefined, pattern: string|undefined};
    constructor(public props: IRenderField) {
        super(props);
        this.field = props;
        this.displayType = props.meta.displayType;
        this.section = props.section;
        this.isFormControl = FormUtils.isFormControl(props.meta);
        this.uuid = FormUtils.getUUID();
        this.validation = {
            required: props?.meta?.validation?.required?true:undefined,
            pattern: props?.meta?.validation?.pattern?props?.meta?.validation?.pattern:undefined
        }
            
    }
    render() {
        if (this.isFormControl) {
            return (<Display form={this.props.form}>
                {
                    this.control()
                }              
            </Display>)
        }
        return (<Fragment></Fragment>)
    }
    control() {
        switch (this.displayType) {
            case 'date':
                return (this.date())
            case 'select':
                return (this.select());
            case 'radio':
                return (this.radio());
            case 'checkbox':
                return (this.checkbox());
            case 'email':
                return (this.email());
            case 'number':
                return (this.number());
            case 'text':
            case 'text_field':
            default:
                return (this.text());
        } 
    }
    checkDependency() {
        return this.context.getFieldDisplay(this.section, this.field.name);
    }
    abstract date(): JSX.Element;
    abstract text(): JSX.Element;
    abstract email(): JSX.Element;
    abstract number(): JSX.Element;
    abstract radio(): JSX. Element;
    abstract checkbox(): JSX. Element;
    abstract select(): JSX.Element;
    abstract button(): JSX.Element;

    onChange(e: React.ChangeEvent<any>) {
        const value = e.target.value;
        this.context.setField(this.section, this.field.name, value);
        const deps = this.context.getDependencies(this.section, this.field.name);
        deps && deps.forEach((dep: {type: string, value: string, field: string, url: string})=>{
            const type = dep.type;
            switch(type) {
                case 'exists': {
                    const depValue = dep.value;
                    const field = dep.field;
                    if (value != undefined) {
                        this.context.setFieldDisplay(this.section, field, value === depValue)
                    }
                }
                break;
                case 'load': {
                    const field = dep.field;
                    const url = dep.url.replace('{0}', value);
                    loader(url).then(res => {
                        this.context.setFieldOptions(this.section, field, res);
                        this.props.sync();
                    });
                }
                break;
            }
        });
        this.props.sync();
    }
}

function loader(url: string) {
    return fetch(url).then(res => res.json());
}

function Display(props: any) {
    // useEffect(() => {
    //     console.log('display updated');
        
    // },[props.form.display])
    return (
        <Fragment>
        {props?.form?.display && props.children}
        </Fragment>
    )
}
