import React, { Fragment } from 'react';
import { MSGS } from "../../constants";
import { IMeta } from "../../constants/model-interfaces";
import BasicFormControl from "./base-form-control";

export default class BSFormControl extends BasicFormControl {
    
    date(): JSX.Element {
        const meta = this.field.meta;
        return (
            <div className="form-group">
                <label htmlFor={'date'+this.uuid}>{meta.displayName}</label>
                <input type="date" className="form-control" id={'date'+this.uuid}
                    required={this.validation.required}
                    min={meta.min} max={meta.max}
                    value={this.props?.form?.value}
                    onChange={this.onChange.bind(this)}
                />
                {this.showValidation(meta)}
            </div>
        )
    }
    input(type: string) {
        const meta = this.field.meta;
        return (
            <div className="form-group">
                <label htmlFor={'input'+this.uuid}>{meta.displayName}{this.validation.required&&<MandatoryLabel/>}</label>
                <input type={type} className="form-control" placeholder={meta.displayName} id={'input'+this.uuid} 
                    value={this.props?.form?.value}
                    required={this.validation.required}
                    pattern={this.validation.pattern}
                    min={this.props?.form?.min}
                    max={this.props?.form?.max}
                />
                {this.showValidation(meta)}
            </div>
        )
    }
    text(): JSX.Element {
        return this.input('text');
    }
    email() {
        return this.input('email');
    }
    number() {
        return this.input('number');
    }
    radio(): JSX.Element {
        const meta = this.field.meta;
        return (
            <div className="form-group">
                <label>{meta.displayName}</label>
                {
                    meta.options ? meta.options.map((option,index) => {
                        return (
                            <div className="form-check" key={index}>
                                <input type="radio" className="form-check-input" 
                                    id={'radio'+this.uuid+index} name={this.field.name}
                                    onChange={this.onChange.bind(this)}
                                    value={option.value}
                                    checked={option.value === this.props?.form?.value}
                                    />
                                <label htmlFor={'radio'+this.uuid+index} className="form-check-label">{option.label}</label>
                            </div>
                        )
                    }): ''
                }
            </div>
        )
    }
    checkbox(): JSX.Element {
        const meta = this.field.meta;
        return (
            <div className="form-group">
                <label>{meta.displayName}</label>
                {
                    meta.options && meta.options.map((option,index) => {
                        return (
                            <div className="form-check" key={index}>
                                <input type="checkbox" className="form-check-input" id={'checkbox'+this.uuid+index} name={this.field.name}/>
                                <label htmlFor={'checkbox'+this.uuid+index} className="form-check-label">{option.label}</label>
                            </div>
                        )
                    })
                }
            </div>
        )
    }
    select(): JSX.Element {
        const meta = this.field.meta;
        return (
            <div className="form-group">
                <label htmlFor={'select'+this.uuid}>{meta.displayName}{this.validation.required&&<MandatoryLabel/>}</label>
                <select className="form-control" id={'select'+this.uuid}
                    required={this.validation.required}
                    value={this.props?.form?.value}
                    onChange={this.onChange.bind(this)}
                >
                    <option selected disabled  value="">Choose...</option>
                    {meta.options ? meta.options.map((option:any) => {
                        return (<option key={option.value} value={option.value}>{option.label}</option>)
                    }): ''
                    }
                </select>
                {this.showValidation(meta)}
            </div>
        )
    }
    button(): JSX.Element {
        throw new Error("Method not implemented.");
    }

    showValidation(meta: IMeta) {
        return (
            <Fragment>
                {
                    this.validation.required && <div className="invalid-feedback">
                        {meta?.validation?.required_detail?.errorMsg?meta.validation.required_detail.errorMsg:MSGS.ERROR_MSG.REQUIRED}
                    </div>
                }
                {
                    this.validation.pattern && <div className="invalid-feedback">
                        {meta?.validation?.pattern_detail?.errorMsg?meta?.validation?.pattern_detail?.errorMsg:MSGS.ERROR_MSG.PATTERN}
                    </div>
                }
            </Fragment>
        )
    }
    
}

function MandatoryLabel() {
    return (
        <span>*</span>
    )
}