import React, { useContext } from "react";
import FormFieldRenderer from './core/form-field-renderer';
import SectionLayout from "./core/SectionLayout";
import { IField, ISchema, ITheme, IUISchema } from "./constants/model-interfaces";
import FormContext from "./core/form-context";
import MetaForm from "./constants/MetaForm";
import FormUtils from "./utils/FormUtil";
import MEventEmitter from "./constants/event-emitter";
import { IFormRenderer } from "./constants/common-interface";

export default class FormRenderer extends React.Component<IFormRenderer> {
    schema: ISchema;
    state: any;
    constructor(props: IFormRenderer) {
        super(props);
        this.state = {
            validated: false
        }
    }
    render() {
        const eventEmitter = new MEventEmitter();
        const metaform = new MetaForm(this.props.schema, eventEmitter);
        return (
            <FormContext.Provider value={metaform}>
                <div className="container">
                    <Form 
                        schema={this.props.schema} 
                        validated={this.state.validated} 
                        validate={this.validate.bind(this)}
                        form={metaform.form}    
                        theme={metaform.theme}
                        onChange={this.props.onChange}
                    />
                </div>
            </FormContext.Provider>
            )
    }

    validate(e: React.SyntheticEvent) {
        this.setState({validated:true});
        e.preventDefault();
    }
}

function Form(props:{schema: ISchema, validated:boolean, validate: React.MouseEventHandler<HTMLButtonElement>, form: any, theme: ITheme, onChange: Function}) {
    const hasSection = FormUtils.hasSections(props.schema.fields);
    const sectionLayout = props.theme.sectionLayout;
    const submit = (e: any) => {
        props.validate(e);
        if (props.onChange) {
            const data = props.form;
            props.onChange(e, data);
        }
    }
    const fn = () => false;
    if (hasSection && sectionLayout !== 'default') {
        return (
            <form className={`needs-validation ${props.validated?'was-validated':''}`} noValidate>
                <SectionLayout fields={props.schema.fields} form={props.form}/>
                <button className="btn btn-default" type="submit" onClick={submit}>Submit</button>
            </form>
        )
    } else {
        {
            return (
                <form className={`needs-validation ${props.validated?'was-validated':''}`} noValidate>
                    {props.schema.fields.map((field: IField) => <FormFieldRenderer {...field} key={field.name} section='default' form={props.form[field.name]} sync={fn}/>)}
                </form>
            )
        }
    }
}