import { IDisplayProps, IField, IMeta } from "./../constants/model-interfaces";

const cssClassMap = {
    'md': 'col-md-',
    'lg': 'col-lg-',
    'sm': 'col-sm-',
    'xs': 'col-xs-',
    'default': 'col-md'
}

export default class FormUtils {
    static getCssClassName(prop: IDisplayProps|undefined) {
        if (!prop) {
            return '';
        }
        return prop.md ? cssClassMap['md'] + prop.md:
                    prop.lg ? cssClassMap['lg'] + prop.lg:
                         prop.sm ? cssClassMap['sm'] + prop.sm: cssClassMap['default'];
    }

    static isFormControl(prop: IMeta) {
        if (prop.type === 'section') {
            return false;
        }
        return true;
    }

    static getVerticalSpacing(theme: string) {
        if (theme) {
            switch(theme) {
                case 'mui':
                    return 'my-md-3';
            }
        }
        return '';
    }

    static getAlignment(align: string) {
        return align ? 'wrow-'+align: '';
    }

    static getPadding(theme: string) {
        if (theme) {
            switch (theme) {
                case 'bootstrap':
                    return 'p-md-3';
                case 'mui':
                    return 'p-md-3';
            }
        }
        return '';
    }

    static getUUID(prefix: string='') {
        const randomNum = Math.floor(Math.random()*100+Date.now());
        return `${prefix}_${randomNum}`;
    }

    static hasSections(fields: Array<IField>) {
        return fields.find((field: IField)=>field.meta.type === 'section')? true : false;
    }
}